@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #404040;
  color: white;
  font-family: "Raleway", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media(prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:#fff}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}
.button{border-radius:50%;width:200px;height:200px;border:none;color:#fff;font-weight:900;font-size:2.5rem;background:red;text-shadow:0 3px 1px rgba(122,17,8,.8);box-shadow:0 8px 0 #b70900,0 15px 20px rgba(0,0,0,.35);text-transform:uppercase;transition:.4s all ease-in;outline:none;cursor:pointer;text-align:center;-webkit-user-select:none;-ms-user-select:none;user-select:none;-webkit-tap-highlight-color:rgba(0,0,0,0);margin-bottom:1rem}.button:active{padding-top:3px;-webkit-transform:translateY(4px);transform:translateY(4px);box-shadow:0 4px 0 #b70000,0 8px 6px rgba(0,0,0,.45)}button span{position:relative}.pressed{padding-top:3px;-webkit-transform:translateY(4px);transform:translateY(4px);box-shadow:0 4px 0 #b70000,0 8px 6px rgba(0,0,0,.45)}.container{display:flex;flex-direction:column;justify-content:center;align-items:center}.video{margin-bottom:3rem}.header{font-size:4.5rem;color:#bdffee}.instructions{color:#bdffee;font-size:1.5rem}
